import React from 'react'
import { Parallax } from 'react-scroll-parallax';
import { makeStyles } from '@material-ui/core'
import zIndex from '@material-ui/core/styles/zIndex';
const useStyles = makeStyles({
    root: {
        display: 'flex',
        flexFlow: 'row wrap',
        alignItems: 'center',
        height: '100vh'
    },
    wrapper :{
        position :'relative',
        width:'25em'
    } ,
    ball : {
        position:'relative',
        zIndex:2
    }
})
function Play() {
    const classes = useStyles()
    return (
        <div className={classes.root}>
            <Parallax  y={[100, 400]} tagOuter="figure" >

                <div className={classes.wrapper}>
                    <svg className ={classes.ball} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1920 1080" width="500" height="500">
                        <path id="Shape 2" class="shp0" d="M424 886C127.59 886 -112 646.41 -112 350C-112 53.59 127.59 -186 424 -186C720.41 -186 960 53.59 960 350C960 646.41 720.41 886 424 886Z" />
                    </svg>

                </div>
            </Parallax>
        </div >
    )
}

export default Play
